<template>
    <div class="orderPayNew" :class='isShowCoverUp?"fiexedClass":""' can-swipe-back="false" ref="orderPayNew">
        <!-- <van-nav-bar class="task-nav-bar" title="订单支付" :border="false" left-arrow @click-left="goBack" /> -->
        <!-- 头部 -->
        <navigation-bar @onLeftClick="goBack" pageName="订单支付" />
        <div class="orderPayNewContent" :class="{themeL:bindThemeL}">
            <div class="orderPayNewScroll" ref="orderPayNewScroll" :class="{stopScrollStyle :isShowCoverUp}">
                <div class="addRessBox">
                    <div class="selectBtn">
                        <div :class="!isSelectBool?'actSelectBtn':'noSelectBtn'" @click="switchBtn(false)">自提</div>
                        <div :class="isSelectBool?'actSelectBtn':'noSelectBtnRight'" @click="switchBtn(true)">配送</div>
                    </div>
                    <div class="selfMentionBox" v-if="isSelectBool">
                        <div class="selfMention">
                            <div>
                                <input type="text" class="pickupName" maxlength="8" style="width:100%"
                                    placeholder="请输入联系人" v-model="recName" @focus="pickupFocus" @blur="pickupBlur">
                            </div>
                            <div>
                                <input type="tel" class="pickupPhone" placeholder="请输入联系电话" v-model="recPhone"
                                    oninput="if(value.length>11)value=value.slice(0,11)"
                                    onkeyup="value=value.replace(/[^\d]/g,'')" @focus="pickupFocus"
                                    @blur="pickupBlur" />
                            </div>
                        </div>
                        <div class="lineBox" v-if="recAddr"></div>
                        <div class="consigneeAddressBox">
                            <div class="consigneeLeftBox" v-if="!recAddr">
                                <div class="hasConsigneeAddressBox">
                                    <div class="consigneeInfo reginInfo" v-if="!recAddr">
                                        <div class="regionRight">
                                            <label for="region">所在地区：</label>
                                            <div class="reginLeftTxt"
                                                v-if="!isVirtualArea||(isVirtualArea&&!regionData.recDistrict)"
                                                @click="showSelectBox()">
                                                {{defaultRegionData.recAreaName}}</div>
                                            <div class="reginLeftTxt" v-if="isVirtualArea&&regionData.recDistrict"
                                                 @click="showSelectBox()">
                                                {{regionData.recProvince}}{{regionData.recCity}}{{regionData.recDistrict}}{{regionData.recAreaName}}
                                            </div>
                                        </div>
                                        <img src="@/assets/images/home/property_payment/icon_arrow.png" alt="">

                                    </div>
                                    <div class="consigneeInfo" v-if="!recAddr">
                                        <label for="site">详细地址：</label>
                                        <input id="site" placeholder="街道、楼牌号等" type="text" v-model="recAddressDetail"
                                            maxlength="50">
                                    </div>
                                </div>
                            </div>
                            <div class="consigneeAddressBox" v-if="recAddr" @click="goRecAddrList()">
                                <img class="consigneePosition" src="@/assets/images/mall/icon_position.png">
                                <div class="recAddress">
                                    <div class="recAddressTxt">
                                        地址：{{recAddr.recAddr}}
                                    </div>
                                </div>
                                <img class="consigneeArrow" src="@/assets/images/home/property_payment/icon_arrow.png">
                            </div>

                        </div>
                        <img class="bottomRibbon" src="@/assets/images/mall/order_address_top_border.png">
                    </div>
                    <div class="selfMentionBox" v-if="!isSelectBool">
                        <div class="selfMention">
                            <div>
                                <input type="text" class="pickupName" maxlength="8" style="width:100%"
                                    placeholder="请输入联系人" v-model="ownerName" @focus="pickupFocus" @blur="pickupBlur">
                            </div>
                            <div>
                                <input type="tel" class="pickupPhone" placeholder="请输入联系电话" v-model="ownerPhone"
                                    oninput="if(value.length>11)value=value.slice(0,11)"
                                    onkeyup="value=value.replace(/[^\d]/g,'')" @focus="pickupFocus"
                                    @blur="pickupBlur" />
                            </div>
                        </div>
                        <div class="lineBox"></div>
                        <div class="consigneeAddressBox">
                            <div class="consigneeLeftBoxSelfMention">
                                <img class="consigneePosition" src="@/assets/images/mall/icon_position.png">
                                <div class="pickUpAddress">
                                    <div class="nameAndPhoneBox">
                                        <div class="consigneeName">{{pickup.pickupName}}</div>
                                        <div class="consigneePhone">{{pickup.pickupPhone}}</div>
                                    </div>
                                    <div class="consigneeAddress">地址：{{pickup.pickupAddr}}</div>
                                </div>
                            </div>
                        </div>
                        <img class="bottomRibbon" src="@/assets/images/mall/order_address_top_border.png">
                    </div>
                </div>
                <div class="payBody">
                    <div class="storeList" v-for="(shopItem,shopIndex) in showInfo.shopList "
                         :key="'shopItem'+shopIndex">
                        <div class="storeNameBox">
                            <div class="storeName">{{shopItem.shopName}}</div>
                        </div>
                        <div class="goodsListBox">

                            <div class="remarkTime"
                                v-if="!shopItem.noDistribution&&shopItem.deliveryDayList&&shopItem.deliveryDayList.length>0">
                                <div class="remarkNameTime">送达时间</div>
                                <div class="deliveryNumTime" v-if="shopItem.deliveryNeedUpdata!=1"
                                     @click="deliveryClick(shopItem,shopIndex)">
                                    {{shopItem.deliveryDefaultString}}</div>
                                <div class="deliveryNumRedTime" v-if="shopItem.deliveryNeedUpdata==1"
                                     @click="deliveryClick(shopItem,shopIndex)">
                                    {{shopItem.deliveryDefaultString}}</div>
                                <img class="storeImg" src="@/assets/images/home/property_payment/more_black.png" alt=""
                                     @click="deliveryClick(shopItem,shopIndex)">
                            </div>
                            <div class="goodsList" v-for="(skuItem,i) in shopItem.skuList" :key="'skuItem'+i">
                                <div class="goodsItemBox" v-if="i<showMoreSku">
                                    <div class="goodsImgBox">
                                        <img class="goodsImg" :src="skuItem.skuPicUrl" alt="" :onerror="defaultImg">
                                        <div class="sellOut" v-if="skuItem.storeNumber==0&&!skuItem.activitySpuId">补货中
                                        </div>
                                        <div class="stockNum"
                                            v-if="skuItem.storeNumber<skuItem.skuCountLimit&&skuItem.storeNumber!=0">
                                            仅剩{{skuItem.storeNumber}}件</div>
                                    </div>
                                    <div class="goodsInfoBox">
                                        <div class="goodsNameAndPrice">
                                            <div class="lightGoodsName" v-if="skuItem.noDistribution">
                                                {{skuItem.spuName}}</div>
                                            <div class="goodsName" v-if="!skuItem.noDistribution">
                                                {{skuItem.spuName}}</div>
                                            <div class="goodsPrice">¥{{skuItem.skuPrice.toFixed(2)}}</div>
                                        </div>
                                        <div class="noDistribution" v-if="skuItem.noDistribution">
                                            商品不在配送范围内，请重选收货地址！
                                        </div>
                                        <div class="goodsSpecificationsBox" v-if="!skuItem.noDistribution">
                                            <div class="goodsSpecifications">{{skuItem.attrValueContent}}</div>
                                            <div class="goodsNum">x{{skuItem.skuCount}}</div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div class="goodsMoreBottom" v-if="shopItem.skuList.length>3" @click="showMore()">
                                <div class="bottomTitle" v-if="showMoreSku==3">共{{shopItem.skuList.length}}件商品</div>
                                <div class="bottomTitle" v-if="showMoreSku!=3">收起</div>
                                <img class="iconImg" :src="showMoreSku!=3?upIcon:downIcon" alt="">
                            </div>
                        </div>
                        <div class="remarkBox">
                            <div class="remark" v-if="shopItem.actList&&shopItem.actList.length>0"
                                 v-for="(actItem,i) in shopItem.actList">
                                <div class="reductionBox">
                                    <div class="reductionTag reductionTagGreen"
                                        v-if="actItem.actType==0||actItem.actType==1">满减</div>
                                    <div class="reductionTag reductionTagRed" v-if="actItem.actType==2">打包</div>
                                    <div class="deduction_name" v-if="actItem.actType==0||actItem.actType==1">
                                        满{{actItem.ruleFull}}减{{actItem.ruleReduction}}</div>
                                    <div class="deduction_name" v-if="actItem.actType==2">
                                        {{actItem.rulePrice}}元{{actItem.ruleAmount}}件</div>
                                </div>
                                <div class="deduction_num" v-if="actItem.actType==0||actItem.actType==1">
                                    -¥{{actItem.ruleReduction}}</div>
                                <div class="deduction_num" v-if="actItem.actType==2">-¥{{actItem.off}}</div>
                            </div>
                            <div class="remark">
                                <div class="reductionBox">
                                    <div class="reductionTag reductionTagYellow">优惠券</div>
                                    <div class="deduction_name">
                                        商家优惠券</div>
                                    <div class="deduction_red_name"
                                         v-if="shopItem.couponConfirm.couponTotal>0&&shopItem.couponConfirm.isDefault">
                                        已使用推荐优惠券</div>
                                </div>

                                <div class="userCouponInfo">
                                    <div class="deduction_num" v-if="shopItem.couponConfirm.couponTotal>0"
                                         @click="shopCouponMore(shopItem,shopIndex)">
                                        -¥{{shopItem.couponConfirm.couponTotal.toFixed(2)}}</div>
                                    <div class="nocouponTxt1"
                                         v-if="(shopItem.couponConfirm.couponTotal==0)&&shopItem.couponUsefulList&&shopItem.couponUsefulList.length>0"
                                         @click="shopCouponMore(shopItem,shopIndex)">
                                        {{shopItem.couponUsefulList.length}}张可用</div>
                                    <div class="nocouponTxt"
                                         v-if="!shopItem.couponUsefulList||shopItem.couponUsefulList&&shopItem.couponUsefulList.length==0"
                                         @click="shopCouponMore(shopItem,shopIndex)">
                                        暂无可用</div>
                                    <img class="couponMoreImg"
                                         src="@/assets/images/home/property_payment/more_black.png" alt=""
                                         @click="shopCouponMore(shopItem,shopIndex)">
                                </div>

                            </div>
                            <div class="remark">
                                <div class="remarkName">包装费</div>
                                <div class="deliveryNum">
                                    ¥{{(parseFloat(shopItem.packFee)).toFixed(2)}}</div>
                            </div>
                            <div class="remark">
                                <div class="remarkName">配送费</div>
                                <div class="deliveryNum" v-if="!shopItem.noDistribution">
                                    ¥{{(parseFloat(shopItem.delivery)).toFixed(2)}}</div>
                                <div class="delivery" v-if="shopItem.noDistribution">
                                    超出配送范围</div>
                            </div>
                            <div class="remark" style="align-items: flex-start;">
                                <div class="remarkName">订单备注</div>
                                <textarea id="remarkTextarea" cols="15" rows="2" maxlength="30" placeholder="写写备注/要求"
                                          type="text" v-model="shopItem.remark"></textarea>
                            </div>
                        </div>
                        <div class="amountBox">
                            <div class="amountName">小计：</div>
                            <div class="amountNum">¥{{shopItem.shopTotal.toFixed(2)}}</div>
                        </div>
                    </div>
                </div>

                <div class="pointInfoBox">
                    <div class="pointDeduction" @click="platformCouponMore()">

                        <div class="point_info">

                            <div class="point_info_img_box">
                            <img class="point_info_img" src="@/assets/images/newSlicing/coupon/red envelope_cut.png" />
                            </div>
                            <div>
                                <div class="point_title">平台红包
                                    <!-- <div class="userCouponImg" style="font-size: 10px;height: 16px;line-height: 16px;border: 0.5px solid rgb(251, 87, 104);
                                                        padding-top: 0px;" v-show="showInfo.couponPlatformDefault == 1">
                                                        已使用推荐优惠
                                                    </div> -->
                                </div>
                                <div class="point_rule">平台红包和店铺优惠券可同享</div>

                            </div>

                        </div>

                        <div class="userCouponInfo">
                            <span v-if="showInfo.couponPlatformTotal!=0">-¥{{(parseFloat(showInfo.couponPlatformTotal)).toFixed(2)}}</span>
                            <span class="nocouponTxt"
                                  v-if="showInfo.couponPlatformTotal==0&&(!showInfo.platGeneralUsefulList&&!showInfo.platShopUsefulList)">暂无可用</span>
                            <span class="nocouponTxt1"
                                  v-if="showInfo.couponPlatformTotal==0&&((showInfo.platGeneralUsefulList||showInfo.platShopUsefulList))&&showInfo.couponPlatformList&&showInfo.couponPlatformList.length == 0">{{couponPlatformCanUseCount}}张可用</span>
                            <img class="couponMoreImg" src="@/assets/images/home/property_payment/more_black.png"
                                 alt="">
                        </div>
                    </div>
                    <div class="pointDeduction">

                        <div class="point_info">
                            <div class="point_info_img_box">
                                 <img class="point_info_img" src="@/assets/images/newSlicing/coupon/integral_cut.png" />
                            </div>
                            <div>
                                <div class="point_title">积分抵扣</div>
                                <div class="point_rule" v-if="this.$userInfo.ROOM_CODE">100积分抵扣1元</div>
                                <div class="no_point_rule" v-if="!this.$userInfo.ROOM_CODE">绑定房产才可使用积分哦</div>
                            </div>

                        </div>

                        <div class="change_point">
                            <img class="delete_icon" @click="numberReduce(pointAviable)"
                                 src="@/assets/images/mall/bg_cut.png" />
                            <input type="tel" onkeyup="value=value.replace(/[^\d]/g,'')" class="point_num"
                                   id="point_num" v-model="integralDeduction" @blur="changePointNum()"
                                   :disabled="!this.$userInfo.ROOM_CODE">
                            <img class="add_icon" @click="numberAdd(pointAviable)"
                                 src="@/assets/images/mall/bg_add.png" />
                        </div>
                    </div>
                    <div class="pointDeductionAll">


                        <div>
                            <div class="point_title">
                                <span>使用全部积分抵扣</span>
                                <div class="point_title_div" @click="integralInstructionFn()">
                                    <img class="point_title_img"
                                         src="@/assets/images/home/property_payment/icon_tips.png">
                                </div>
                            </div>
                            <div class="point_rule">本次可用最高积分为{{pointAviable}}</div>
                        </div>
                        <van-switch v-model="integralAll" active-color="#17B7BD" inactive-color="#D1D1D1" size="26px"
                                    @change="integralAllFn(pointAviable)" />
                    </div>
                </div>
                <div class="total_and_deduction">
                    <div class="total_money">
                        <div class="total_name">应付总额</div>
                        <div class="total_num">￥{{(parseFloat(showInfo.shouldPayTotal)).toFixed(2)}}</div>
                        <!-- <div class="total_num" v-if="!isSelectBool">￥{{(parseFloat(saleInfoPickup.shouldPayTotal)).toFixed(2)}}</div> -->
                    </div>
                    <div class="total_money">
                        <div class="total_name">积分抵扣</div>
                        <div class="total_num">-￥{{pointAviablePersent}}</div>
                    </div>
                </div>
                <div class="pointInfoBox">
                    <div class="nostoreBox">
                        <div class="userCouponTitle">如遇缺货</div>
                        <div class="userCouponInfo">
                            <div class="nocouponTxt" @click="outClick()">
                                {{nostoreString}}</div>
                            <img class="couponMoreImg" src="@/assets/images/home/property_payment/more_black.png" alt=""
                                 @click="outClick()">
                        </div>
                    </div>
                </div>
                <div :style="placeholderStyle"></div>
                <div ref="placeHolder" style="height:200px" v-show="isShowPlaceHolder"></div>
            </div>

            <div class="payBox bar-footer" :style="{paddingBottom:this.$footBarHeight+'px'}">
                <div class="secksellMsg" v-show="hasSecksellGoods">秒杀商品不参与其他活动哦</div>
                <div class="actualPayBox">


                    <div class="actualPay">
                        <div class="actualName">实际支付：</div>
                        <div class="actualNum">￥{{(showInfo.realPayTotal - parseFloat(this.pointAviablePersent)).toFixed(2)}}</div>
                        <!-- <div class="actualNum" v-if="!isSelectBool">￥{{realPayTotal}}</div> -->
                    </div>
                    <div class="payBtn" @click="goToPay()">立即支付</div>
                </div>
            </div>
        </div>
        <van-popup v-model="showPicker" position="bottom">
            <van-picker ref="deliveryPicker" show-toolbar :columns="columns" @cancel="showPicker = false"
                @confirm="pickerOnConfirm" @change="pickerOnChange" confirm-button-text="确定" title="选择送达时间" />
        </van-popup>


        <van-popup v-model="showOutPicker" position="bottom">
            <van-picker ref="outPicker" show-toolbar :columns="outColumns" @cancel="showOutPicker = false"
                @confirm="outPickerOnConfirm" confirm-button-text="确定" title="如遇缺货" />
        </van-popup>

        <van-popup v-model="payWindowShow" position="bottom" class="vanPopup" @close="closePopup">
            <div class="payModeBox">
                <div class="payModeTitle">支付方式</div>
                <div v-show="isAcppay" class="payModeItem" @click="alipay(2)">
                    <div class="unipayclass">
                        <img class="unipayIcon" src="@/assets/images/common/icon_unipay.png" alt="">
                        <div style="display: inline-block; vertical-align:middle;">云闪付</div>
                        <img class="iconUnipayRight" src="@/assets/images/common/icon_unipay_right.png" alt=""
                            style="display: inline-block;vertical-align:middle;width: 21px;height: 21px;">
                        <div class="saleIcon" style="display: inline-block;vertical-align:middle;" v-if="isAcpPayTag">
                            {{acpPayTag}}</div>
                    </div>
                </div>
                <div v-show="isAlipay" class="payModeItem" @click="alipay(1)">
                    <img src="@/assets/images/common/icon_alipay.png" alt="">
                    <div>支付宝</div>
                </div>
<!--                <div v-show="isWechart" class="payModeItem" @click="alipay(0)">-->
<!--                    <img src="@/assets/images/common/icon_wechart.png" alt="">-->
<!--                    <div>微信</div>-->
<!--                </div>-->
            </div>
            <div class="cancelBtn" @click="closePopup">取消</div>
        </van-popup>
        <div class="coverUpBox" v-if="isShowCoverUp" @click="hideCoverUp()"></div>
        <div class="couponBox" v-if="isShowCoverUp">
            <div class="couponHead">
                <div class="commonHead" @click="hideCoverUp()"><img src="@/assets/images/common/left_black.png">
                </div>
                <div class="title">优惠券</div>
            </div>
            <div class="btnList" style="position: absolute;width:100%;top: 45px;">
                <div class="btnItem" style="z-index: 11;" @click="setBtnItemIndex(0)">
                    <div class="btnItemTxt" :class="{'showItemTxt':btnItemIndex == 0}">
                        可用优惠券({{couponUsefulCount}})
                    </div>
                    <div class="signBox" v-if="btnItemIndex == 0">
                        <span class="sign"></span>
                    </div>
                </div>
                <div class="btnItem" style="z-index: 11;" @click="setBtnItemIndex(1)">
                    <div class="btnItemTxt" :class="{'showItemTxt':btnItemIndex == 1}">
                        不可用优惠券({{couponUselessCount}})</div>
                    <div class="signBox" v-if="btnItemIndex == 1">
                        <span class="sign"></span>
                    </div>
                </div>
            </div>
            <div class="selectContent yesSelectContent" has-bouncing='false' v-if="btnItemIndex == 0">
                <div class="couponList" v-if="couponUsefulList.length!=0">

                    <div class="couponUseClass" v-for="(couponUsefulListItem,i)  in couponUsefulList"
                         :key="'couponUsefulListItem'+i">


                        <div class="couponItem"
                             :class="couponItem.isShowScope?'couponItemMinMargin':'couponItemMaxMargin'"
                             v-for="(couponItem,couponIndex) in couponUsefulListItem" :key="'couponItem'+couponIndex">
                            <div class="couponScopeCover" @click.stop="showAndHideScope(couponItem)"></div>
                            <div class="couponCover" v-show="!couponItem.isSelect&&couponUsefulListItem.isSelect"
                                 @click.stop="selectedCoupon(couponUsefulListItem,couponIndex,couponItem)"></div>
                            <div class="couponBg"></div>
                            <div class="couponContent" :class="setCouponBg(couponItem)"
                                 @click.stop="selectedCoupon(couponUsefulListItem,couponIndex,couponItem)">
                                <img v-show="couponItem.isSelect" class="couponSelect"
                                     src="@/assets/images/newSlicing/coupon/select_cut.png" alt="">

                                <div class="couponPriceBox">
                                    <div class="couponPrice">
                                        <div class="couponPriceIcon">￥</div>
                                        <div class="couponPriceNum">{{couponItem.oldCouponTotal}}</div>
                                    </div>
                                    <div class="couponPriceTxt">满{{couponItem.limitTotal}}元可用</div>
                                </div>
                                <div class="couponInfoBox">
                                    <div class="couponInfo">
                                        <div class="couponName" v-show="couponItem.type!='02'">{{couponItem.name}}</div>
                                        <div class="couponName" v-show="couponItem.type=='02'">{{couponItem.shopName}}优惠券</div>
                                        <div class="couponTime"
                                             v-if="couponItem.effType == '1'||couponItem.effBeginDay">
                                            {{couponItem.effBeginDay}}-{{couponItem.effEndDay}}</div>
                                        <div class="couponTime"
                                             v-if="couponItem.effType == '2'&&couponItem.effDays == 1&&!couponItem.effBeginDay">
                                            领取后当天有效
                                        </div>
                                        <div class="couponTime"
                                             v-if="couponItem.effType == '2'&&couponItem.effDays != 1&&!couponItem.effBeginDay">
                                            领取后{{couponItem.effDays}}天内有效
                                        </div>
                                        <div class="couponScope" @click.stop="showAndHideScope(couponItem)">

                                            <div class="couponScopeTxt">适用范围</div>
                                            <img v-show="couponItem.type!='02'"
                                                 :class="couponItem.isShowScope?'rotateImg':''" class="couponScopeIcon"
                                                 src="@/assets/images/newSlicing/coupon/white_arrow_down_cut.png" alt="">
                                            <img v-show="couponItem.type=='02'"
                                                 :class="couponItem.isShowScope?'rotateImg':''" class="couponScopeIcon"
                                                 src="@/assets/images/newSlicing/coupon/arrow_down_cut.png" alt="">
                                        </div>
                                    </div>
                                    <!-- <div class="couponBtnBox">
                                        <div class="couponBtn" v-if="couponItem.isPicked==1||couponItem.status=='01'"
                                            @click="goToShopPage(couponItem)">立即使用</div>
                                        <div class="couponBtn" v-if="couponItem.isPicked==0&&couponItem.isFinished==0"
                                            @click="receiveBtn(couponItem)">立即领取</div>
                                    </div> -->
                                </div>
                            </div>
                            <div class="couponTypeBox" v-show="couponItem.isShowScope">
                                <div class="couponTypeTxt">
                                    <div>{{couponItem.shopScope}}</div>
                                </div>
                            </div>
                        </div>
                        <div class="separator" v-show="i<couponUsefulList.length-1"></div>
                    </div>
                </div>
                <div style="height: 50px" v-if="couponUsefulList.length!=0"></div>
                <div class="noDataBox" v-if="couponUsefulList.length==0">
                    <img class="noDataImg" src="@/assets/images/default/no_coupon_img.png" alt="">
                    <div class="noDataTxt">暂无优惠券</div>
                </div>
            </div>
            <div class="selectContent noSelectContent" has-bouncing='false' v-if="btnItemIndex == 1">
                <div class="couponList" v-if="couponUselessList.length!=0">
                    <div class="couponUseClass" v-for="(couponUselessListItem,i)  in couponUselessList"
                         :key="'couponUselessListItem'+i">

                        <div class="couponItem"
                             :class="couponItem.isShowScope?'couponItemMinMargin':'couponItemMaxMargin'"
                             v-for="(couponItem,couponIndex) in couponUselessListItem" :key="'couponItem'+couponIndex">
                            <div class="couponScopeCover" @click.stop="showAndHideScope(couponItem)"></div>
                            <div class="couponGoDetailCover" @click="goToShopPage(couponItem)"></div>
                            <div class="couponCover"></div>
                            <div class="couponBg"></div>
                            <div class="couponContent" :class="setCouponBg(couponItem)">

                                <div class="couponPriceBox">
                                    <div class="couponPrice">
                                        <div class="couponPriceIcon">￥</div>
                                        <div class="couponPriceNum">{{couponItem.couponTotal}}</div>
                                    </div>
                                    <div class="couponPriceTxt">满{{couponItem.limitTotal}}元可用</div>
                                </div>
                                <div class="couponInfoBox">
                                    <div class="couponInfo">
                                        <div class="couponName" v-show="couponItem.type!='02'">{{couponItem.name}}</div>
                                        <div class="couponName" v-show="couponItem.type=='02'">{{couponItem.shopName}}优惠券</div>
                                        <div class="couponTime"
                                             v-if="couponItem.effType == '1'||couponItem.effBeginDay">
                                            {{couponItem.effBeginDay}}-{{couponItem.effEndDay}}</div>
                                        <div class="couponTime"
                                             v-if="couponItem.effType == '2'&&couponItem.effDays == 1&&!couponItem.effBeginDay">
                                            领取后当天有效
                                        </div>
                                        <div class="couponTime"
                                             v-if="couponItem.effType == '2'&&couponItem.effDays != 1&&!couponItem.effBeginDay">
                                            领取后{{couponItem.effDays}}天内有效
                                        </div>
                                        <div class="couponScope" @click="showAndHideScope(couponItem)">

                                            <div class="couponScopeTxt">适用范围</div>
                                            <img v-show="couponItem.type!='02'"
                                                 :class="couponItem.isShowScope?'rotateImg':''" class="couponScopeIcon"
                                                 src="@/assets/images/newSlicing/coupon/white_arrow_down_cut.png" alt="">
                                            <img v-show="couponItem.type=='02'"
                                                 :class="couponItem.isShowScope?'rotateImg':''" class="couponScopeIcon"
                                                 src="@/assets/images/newSlicing/coupon/arrow_down_cut.png" alt="">
                                        </div>
                                    </div>
                                    <div class="couponBtnBox">
                                        <div class="couponBtn" @click="goToShopPage(couponItem)">去凑单</div>

                                    </div>
                                </div>
                            </div>
                            <div class="couponTypeBox" v-show="couponItem.isShowScope">
                                <div class="couponTypeTxt">
                                    <div>{{couponItem.shopScope}}</div>
                                </div>
                                <div class="restLimitTotalTxt">
                                    差{{couponItem.restLimitTotal}}元可用该券
                                </div>

                            </div>
                        </div>
                    </div>
                </div>
                <div class="noDataBox" v-if="couponUselessList.length==0">
                    <img class="noDataImg" src="@/assets/images/default/no_coupon_img.png" alt="">
                    <div class="noDataTxt">暂无优惠券</div>
                </div>
            </div>
            <div class="completeBtn" v-if="btnItemIndex == 0" @click="completeBtn()">完成</div>
        </div>
        <!-- 配送地址选择 -->
        <div class="fixedLayer" v-if="isShowSelectBox" @click="hideSelectBox()"></div>
        <div class="setReginBox" v-if="isShowSelectBox">
            <div class="setReginHead">
                <div class="retreatBtn" @click="hideSelectBox()">
                    <img src="@/assets/images/common/left_black.png" alt="">
                </div>
                <div class="title">配送至</div>
            </div>
            <div class="selectBox">
                <div class="pitchUpTxt" v-if="regionData.recProvince" @click="queryProvince()">
                    {{regionData.recProvince}}</div>
                <div class="pitchUpTxt city" v-if="regionData.recCity" @click="getCityList(regionData.recProvince)">
                    {{regionData.recCity}}</div>
                <div class="pitchUpTxt district" v-if="regionData.recDistrict"
                    @click="queryDistrict(regionData.recCity)">{{regionData.recDistrict}}</div>
                <div class="pitchUpTxt district" v-if="regionData.recAreaName"
                    @click="getAreaList(regionData.recDistrict)"></div>
                <div class="plaseSelect" v-if="!regionData.recAreaName">
                    <div class="selectTxt">请选择</div>
                    <div class="signBox">
                        <span class="sign"></span>
                    </div>
                </div>
            </div>
            <div ref="adressSelectContent" class="adressSelectContent" scrollbar-y='false' delegate-handle="myScroll"
                has-bouncing='false'>
                <div class="selectItem" v-if="selectDataIndex==0" v-for="(selectItem,i) in selectData"
                    :class="{'pitchUpItem':selectIndex == i}" @click="selectCity(i)">
                    <span>{{selectItem.provinceName}}</span>
                    <img src="@/assets/images/mall/user_addressList_icon_choice.png" alt="">
                </div>
                <div class="selectItem" v-if="selectDataIndex==1" v-for="(selectItem,i) in selectData"
                    :class="{'pitchUpItem':selectIndex == i}" @click="selectCity(i)">
                    <span>{{selectItem.cityName}}</span>
                    <img src="@/assets/images/mall/user_addressList_icon_choice.png" alt="">
                </div>
                <div class="selectItem" v-if="selectDataIndex==2" v-for="(selectItem,i) in selectData"
                    :class="{'pitchUpItem':selectIndex == i}" @click="selectCity(i)">
                    <span>{{selectItem.districName}}</span>
                    <img src="@/assets/images/mall/user_addressList_icon_choice.png" alt="">
                </div>
                <div class="selectItem" v-if="selectDataIndex==3" v-for="(selectItem,i) in selectData"
                    :class="{'pitchUpItem':selectIndex == i}" @click="selectCity(i)">
                    <span>{{selectItem.areaName}}</span>
                    <img src="@/assets/images/mall/user_addressList_icon_choice.png" alt="">
                </div>
            </div>
        </div>
        <!-- 超出限购弹窗 -->
        <div class="outSaleAlert" v-if="isShowoutSaleAlert" @click="hideoutSaleAlert()">
            <div class="outSaleAlertContent">

                <div class="outSaleAlertTopBox">
                    <div class="outSaleListBox" v-if="overStoreList.length>0">

                        <div class="outSaleListTitle">以下商品促销库存不足，现需按如下价格购买，请重新确认</div>

                        <div class="outSaleListGoodsBox" v-for="(skuItem,i) in overStoreList">
                            <img class="outSaleListGoodsImg" :src="skuItem.skuPicUrl" alt="">
                            <div class="outSaleListGoodsDetailBox">
                                <div class="outSaleListGoodsTitle">{{skuItem.spuName}}</div>
                                <div class="outSaleListGoodsPriceBox">
                                    <div class="outSaleListGoodsPrice">
                                        ￥{{skuItem.skuPrice}}
                                    </div>
                                    <div class="outSaleListGoodsNum">
                                        x{{skuItem.skuCount}}
                                    </div>
                                </div>
                            </div>
                        </div>

                    </div>

                    <div class="line" v-if="overStoreList.length>0&&overLimitList.length>0"></div>
                    <div class="outSaleListBox" v-if="overLimitList.length>0">

                        <div class="outSaleListTitle">以下商品已超出限购数量，现需按如下价格购买，请重新确认</div>

                        <div class="outSaleListGoodsBox" v-for="(skuItem,i) in overLimitList">
                            <img class="outSaleListGoodsImg" :src="skuItem.skuPicUrl" alt="">
                            <div class="outSaleListGoodsDetailBox">
                                <div class="outSaleListGoodsTitle">{{skuItem.spuName}}</div>
                                <div class="outSaleListGoodsPriceBox">
                                    <div class="outSaleListGoodsPrice">
                                        ￥{{skuItem.skuPrice}}
                                    </div>
                                    <div class="outSaleListGoodsNum">
                                        x{{skuItem.skuCount}}
                                    </div>
                                </div>
                            </div>
                        </div>

                    </div>

                </div>
                <div class="outSaleButton" @click="hideoutSaleAlert()">
                    我知道了
                </div>

            </div>



        </div>
    </div>
</template>
<script>
    /*
    一、首次进入
    1.通过上一页面传值的店铺列表请求最新sku信息（modifySkuInfo），通过返回参数计算页面显示金额。同步校验收货地址（如果收货地址存在），如果非自提显示商品是否配送
    2.使用最新sku信息请求收货地址和自提信息及推荐优惠券
    3.计算可用积分金额并请求可用积分
    二、选择收货地址后返回本界面
    1.用户点击地址栏，跳转收货地址页
    2.返回支付页后，如果收货地址不为空，校验收货地址并重置积分和优惠券金额，校验后请求可用积分接口

    三、支付
    1.用户选择积分和优惠券之后，计算实际支付金额
    2.点击立即支付时请求支付订单参数，然后弹出支付方式(如果超出限购或者库存不足，弹窗提示并更新页面)
    3.用户选择取消时，弹出提示框是否放弃，放弃跳转订单列表，继续支付弹出支付方式
    4.用户选择支付方式后调用支付sdk，跳转支付app
    5.用户返回信我家时，监听收到支付完成消息，调用查询订单接口查看支付情况，支付成功跳转支付成功页，支付失败跳转订单列表页


    三、优惠券
    1.点击店铺列表下方的优惠券按钮时请求优惠券（可用和不可用）
    2.选择优惠券后重置积分并获取最新可用积分（可用积分为自营店铺总金额减去自营店铺优惠券金额）
    */
    import { mapState } from "vuex";
    import { Toast, Dialog } from "vant";
    import { storage } from '../../utils/storage';
    import { store } from "../../utils/store";
    import NavigationBar from '@/common/currency/NavigationBar.vue';
    import upIcon from "@/assets/images/mall/mall_icon_up.png";
    import downIcon from "@/assets/images/mall/mall_icon_down.png";
    import Config from "../../common/config";
    import {
        modifySkuInfoService,
        refreshSaleInfoService,
        createOrderService,
        orderConfirmService,
        queryUsefulListService,
        changeRecAddrService,
        orderDetailService,
        getUseOrderLimitService,
        quickAddRecAddrService,
    } from '../../services/productOrderService.js';
    import {
        delRecAddrService,
        queryProvinceService,
        getCityListService,
        queryDistrictService,
        getVirtualAreaCodesService,
        getAreaListService
    } from '../../services/shippingAddressService.js';
    import {unionOrderPrepayV34Service} from "../../services/productOrderService";




    export default {
        name: 'orderPayNew',
        components: { NavigationBar },
        data() {
            return {
                goodsData: {},//页面传值
                shopList: [],//店铺列表
                pickupShopList: [],//自提店铺列表
                payParam: {
                    payWay: ""
                },//订单支付参数
                integralDeduction: 0,//按钮加减得积分
                pointAviable: 0,//最多可扣积分
                pointAviablePersent: "0.00",//最多可扣积分对应抵扣金额
                integralAll: false,//是否全部积分抵扣
                // bill_amount: 0.00,//订单应付金额
                // deliveryTotal: 0.00,//配送费总计
                // packFeeTotal: 0.00,//包装费总计
                pickupInfo: {
                    "bill_amount": 0.00,
                    "deliveryTotal": 0.00,
                    "packFeeTotal": 0.00,
                    "canUsePointAmount": 0.00,
                    "couponPlatformTotal": 0.00,
                    coupon: {
                        "shopCode": null,
                        "couponCode": null,
                        "couponTotal": "0",
                    }
                },
                addrInfo: {
                    "bill_amount": 0.00,
                    "deliveryTotal": 0.00,
                    "packFeeTotal": 0.00,
                    "canUsePointAmount": 0.00,
                    "couponPlatformTotal": 0.00,
                    coupon: {
                        "shopCode": null,
                        "couponCode": null,
                        "couponTotal": "0",
                    }
                },
                billCodePoint: 0.00,//订单实付金额
                canUsePointAmount: 0.00,//可用积分的金额
                reductionAmount: 0.00,//当前全部优惠的金额
                recAddrListData: [],//地址列表数据
                recAddr: {},//当前收货地址
                slidesCount: false,
                isAllowPay: false,
                isAllowPayPickup: false,
                deliveryMode: "1",//支持配送类型，1仅配送，2自提+配送，3仅自提
                deliveryModeDesc: "网络请求中，请稍后",
                pickup: {},//自提信息
                coupon: {
                    "shopCode": null,
                    "couponCode": null,
                    "couponTotal": "0",
                },//优惠券
                isSelectBool: false,// 显示顶部选择列表 true配送 false自提
                isCouponBool: true,// true 使用优惠券 false未使用优惠券
                ownerName: "",//自提人姓名
                ownerPhone: "",//自提人电话
                selectCouponIndex: -1,//优惠券选中索引
                isShowCoverUp: false,//是否显示优惠券列表弹窗
                payWindowShow: false,//支付宝微信弹窗
                isWechart: false,//支付选择
                isAlipay: false,//支付选择
                isAcppay: false,//云闪付支付选择
                acpPayTag: '',//满减标签
                isAcpPayTag: false,//是否有满减标签
                isShowPopUp: false,
                isSaveOrder: false,//支付标记
                isShowSelectBox: false,//是否显示选择城市盒子 false 隐藏  true 显示
                recName: "",  //收货人
                recPhone: "", //手机号码
                regionData: {}, //收货地区
                defaultRegionData: {},
                recAddressDetail: "",//详细地址
                selectDataIndex: 0, // 0 获取省 1 市 2 区 3 小区
                recAddrItem: {},
                selectData: [],//存储选择列表数据
                isShowPlaceHolder: false,//键盘弹出时底部占位
                showPicker: false,//配送时间选择弹窗
                showOutPicker: false,//缺货选择弹窗
                columns: [],//配送时间选择器数据源
                outColumns: [],//缺货选择器数据源
                pickerIndex: 0,//配送时间选择器索引
                outPickerIndex: 0,//缺货选择索引
                nostoreStringList: [],//缺货数据源数组
                nostoreChoiceList: [],//缺货数据源索引数组
                nostoreString: "其他商品继续配送，缺货商品退款",
                nostoreChoice: 1,
                hasSecksellGoods: false,
                defaultImg: 'this.src="' + require('../../assets/images/default/default_picture.png') + '"',
                themeCurrent: 0,
                virtualAreaCodes: [],//虚拟小区集合
                isVirtualArea: false,
                /*大字模式*/
                bindThemeL: this.$curTheme == 0 ? false : true,
                isShowoutSaleAlert: false,//超出限购弹窗
                overStoreList: [],//超库存的SKU列表
                overLimitList: [],//超限购的SKU列表
                showMoreSku: 3,
                upIcon: upIcon,
                downIcon: downIcon,

                saleInfo: {
                    'realPayTotal': 0,
                    'couponPlatformTotal': 0,
                    'shopList': [],
                    'couponPlatformDefault': null,
                    'couponPlatformList': []
                },//配送
                saleInfoPickup: {
                    'realPayTotal': 0,
                    'couponPlatformTotal': 0,
                    'shopList': [],
                    'couponPlatformDefault': null,
                    'couponPlatformList': []
                },//自提
                showInfo: {
                    'realPayTotal': 0,
                    'couponPlatformTotal': 0,
                    'shopList': [],
                    'couponPlatformDefault': null,
                    'couponPlatformList': []
                },//当前展示
                couponUsefulList: [],
                couponUselessList: [],
                couponListType: -1,//当前优惠券弹窗(店铺券和平台券区分处理,0-i店铺,-1平台)
                couponUselessCount: 0,
                couponUsefulCount: 0,
                couponPlatformCanUseCount: 0,
            }
        },
        mounted() {
            this.nostoreStringList = ['其他商品继续配送，缺货商品退款', '缺货时电话与我沟通'];
            this.nostoreChoiceList = [1, 2];
            if (this.$devicePlatform == 'android') {
                let el = document.querySelector(".userCouponImg");
                if (el) {
                    el.style.paddingTop = '1px';
                }
                let el1 = document.querySelector(".storeImg");
                if (el1) {
                    el1.style.marginTop = '2px';
                }
            }

            document.querySelector(".orderPayNew").addEventListener('touchmove', function (e) {
                if (document.activeElement)
                    document.activeElement.blur();
            }, { passive: false });
        },
        activated() {
            this.themeCurrent = this.$curTheme;
            //添加键盘监听,让第一响应者滚动到键盘上方显示
            window.addEventListener("native.keyboardshow", this.moveInputIntoView);
            window.addEventListener("native.keyboardhide", this.resetInputIntoView);
            if (this.$route.params.goodsData) {
                this.clearData();
                this.defaultRegionData.recAreaName = this.$userInfo.AREA_NAME;
                this.defaultRegionData.recAreaCode = this.$userInfo.AREA_CODE;
                this.regionData.recAreaName = this.$userInfo.AREA_NAME;
                this.regionData.recAreaCode = this.$userInfo.AREA_CODE;
                if (this.$userInfo.BUILD_NAME) {
                    this.recAddressDetail = this.$userInfo.BUILD_NAME + this.$userInfo.ROOM_NAME;
                }
                this.goodsData = this.$route.params.goodsData;
                this.shopList = this.goodsData.goodsList;
                this.$userInfo.PRODUCT_BILL_CODE = "";//记录订单号码


                //获取自提信息

                let pickupData = storage.getStorage("pickupData");

                if (!pickupData.$s_code_$exp) {
                    this.ownerName = storage.getStorage("pickupData").pickupName;
                    this.ownerPhone = storage.getStorage("pickupData").pickupPhone;
                }

                store.set('updatePageData', (pagData) => {
                    this.regionData = pagData;
                })
                this.queryVirtualAreaCodes();//查询虚拟小区集合
                //拉取最新SKU信息
                this.modifySkuInfoFun();
            }
            this.selectCouponIndex = -1;
            //!获取缓存中的收货信息
            let recAddrTemp = storage.getStorage("recItem");
            if (!recAddrTemp.$s_code_$exp) {
                this.recAddr = storage.getStorage("recItem");
                this.recName = this.recAddr.recName;
                this.recPhone = this.recAddr.recPhone;
                this.changeRecAddr();
                this.pointAviablePersent = "0.00";
                this.integralDeduction = 0;
                this.integralAll = false;
                this.isShowCoverUp = false;
                storage.deleteStorage("recItem");
            }
            this.isShowCoverUp = false;
            window.callback_pay_online=this.callback_pay_online;
        },
        beforeRouteLeave(to, from, next) {
            //移除键盘监听,让第一响应者滚动到键盘上方显示
            window.removeEventListener("native.keyboardshow", this.moveInputIntoView);
            window.removeEventListener("native.keyboardhide", this.resetInputIntoView);
            if (to.name == "shippingAddressList" || to.name == "mapForAddress" || to.name == "readTextPage") {

            } else {
                /* 取消监听，避免重复监听 */
                // if (this.$devicePlatform != "pc") {
                //     window.broadcaster.removeEventListener("newPayBack", this.payCallBack);
                // }
                this.clearData();
            }
            next();
        },
        computed: mapState({
            setCouponBg() {
                return function (couponItem) {
                    if (couponItem.type == '02') {
                        if (couponItem.status) {
                            if (couponItem.status == '01') {
                                return 'stayCouponBg';
                            } else if (couponItem.status == '02') {
                                return 'beenUsedCouponBg';
                            } else {
                                return 'pastdueCouponBg';
                            }
                        } else {
                            if (couponItem.isFinished == 1 && couponItem.isPicked != 1) {
                                return 'staleCouponBg';
                            } else if (couponItem.isPicked == 1) {
                                return 'getCouponBg';
                            } else {
                                return 'stayCouponBg';
                            }
                        }

                    } else {
                        if (couponItem.status) {
                            if (couponItem.status == '01') {
                                return 'terraceBg';
                            } else if (couponItem.status == '02') {
                                return 'beenUsedTerraceBg';
                            } else {
                                return 'pastdueTerraceBg';
                            }
                        } else {
                            if (couponItem.isFinished == 1 && couponItem.isPicked != 1) {
                                return 'staleTerraceBg';
                            } else if (couponItem.isPicked == 1) {
                                return 'getTerraceBg';
                            } else {
                                return 'terraceBg';
                            }
                        }
                    }
                }

            },

            placeholderStyle: function () {
                let hv = this.$stbHeight + this.$footBarHeight + 135 + 'px !important'; //68px
                return { height: hv };
            },
            eventBackButtonFlag: state => state.goBack.eventBackButtonFlag
        }),
        watch: {



            /*监控到物理返回键点击状态*/
            eventBackButtonFlag(val) {
                if (val) {
                    this.$store.dispatch('setEventBackButtonFlag', false);

                    if (this.isShowSelectBox) {
                        this.isShowSelectBox = false;
                    } else if (this.isShowCoverUp) {
                        this.isShowCoverUp = false;
                    } else if (this.showPicker) {
                        this.showPicker = false;
                    } else if (this.showOutPicker) {
                        this.showOutPicker = false;
                    } else if (this.payWindowShow) {
                        this.closePopup();
                    } else if (this.isShowoutSaleAlert) {
                        this.isShowoutSaleAlert = false;
                    } else {
                        this.$router.go(-1);
                    }
                }
            }
        },
        filters: {
            threshold(data) {
                return data >= 10000 ? Math.floor(data / 10000 * 100) / 100 + '万' : data
            }
        },
        methods: {
            inputFocus(e) {
                let UA = window.navigator.userAgent.toLowerCase();
                if (UA.indexOf('iphone') > -1) {
                    if (e && e.target && e.target.tagName) {
                        // 输入框失去焦点，要把IOS键盘推出页面的滚动部分还原。即将页面滚动到视窗顶部对齐
                        this.blurTimer =setTimeout(() => {
                            window.scrollTo(0,0);
                        }, 0)
                    }
                }
            },
            moveInputIntoView() {
                this.isShowPlaceHolder = true;
                if (document.activeElement.id == "point_num") {
                    this.$nextTick(() => {
                        var actualTop = document.activeElement.parentNode.parentNode.parentNode.offsetTop;
                        console.log("document.activeElement.actualTop");
                        console.log(actualTop);
                        this.$refs.orderPayNewScroll.scrollTop = actualTop;
                    });
                }

                if (document.activeElement.id == "remarkTextarea") {
                    this.$nextTick(() => {
                        var actualTop = document.activeElement.parentNode.parentNode.offsetTop;
                        console.log("document.activeElement.actualTop");
                        console.log(actualTop);
                        this.$refs.orderPayNewScroll.scrollTop = actualTop;
                    });
                }
            },
            //键盘收回事件
            resetInputIntoView(e) {
                this.isShowPlaceHolder = false;
                this.changePointNum();
            },
            pickupFocus() {
                this.$refs.orderPayNew.parentElement.parentElement.parentElement.style.position = 'Fixed';
            },
            pickupBlur() {
                this.$refs.orderPayNew.parentElement.parentElement.parentElement.style.position = '';
            },
            showMore() {
                if (this.showMoreSku == 3) {
                    this.showMoreSku = 1000;
                } else {
                    this.showMoreSku = 3;
                }
            },
            // 获取虚拟小区
            queryVirtualAreaCodes: function () {
                getVirtualAreaCodesService().then(res => {
                    if (res.resultCode == 0) {
                        this.virtualAreaCodes = res.data;

                        if (this.virtualAreaCodes.indexOf(this.$userInfo.AREA_CODE) !== -1) {
                            this.isVirtualArea = true;
                            this.defaultRegionData.recAreaName = "请选择省/市/区";
                            this.defaultRegionData.recAreaCode = "";
                            this.regionData.recAreaName = "";
                            this.regionData.recAreaCode = "";
                            this.recAddressDetail = "";
                        } else {
                            this.isVirtualArea = false;
                        }
                    }
                    console.log(res)
                }).catch(error => {
                })
            },
            //拉取最新SKU信息
            modifySkuInfoFun: function () {
                modifySkuInfoService(this.shopList).then(res => {
                    if (res.resultCode == "0") {
                        this.shopList = res.data;
                        //计算订单总金额，生成spu列表
                        for (var i = 0; i < this.shopList.length; i++) {
                            //单个店铺总金额
                            this.shopList[i].shopTotal = 0;
                            for (var j = 0; j < this.shopList[i].skuList.length; j++) {
                                if (this.shopList[i].skuList[j].activitySpuId) {

                                    this.hasSecksellGoods = true;
                                }
                                //计算单个商品金额
                                this.shopList[i].skuList[j].skuTotal = Math.round(this.shopList[i].skuList[j].skuPrice * this.shopList[i].skuList[j].skuCount * 100) / 100;
                                //计算单个店铺
                                this.shopList[i].shopTotal += this.shopList[i].skuList[j].skuTotal;
                                this.shopList[i].remark = "";

                            }

                        }
                        this.$forceUpdate();
                        //获取默认收货地址及自提信息
                        this.orderConfirm();
                    } else {
                        Dialog.alert({
                            message: res.msg
                        }).then(() => {
                            this.goBack();
                        });
                    }

                })
                    .catch(error => {
                        Dialog.alert({
                            message: '网络异常，请稍后重试！'
                        }).then(() => {
                            this.goBack();
                        });
                    })
            },
            // 显示选择城市盒子
            showSelectBox: function () {
                if (this.isVirtualArea) {
                    // 地图选址界面
                    this.$router.push({ name: 'mapForAddress' });
                } else {
                    this.isShowSelectBox = true;
                }
            },
            // 隐藏选择城市盒子
            hideSelectBox: function () {
                this.isShowSelectBox = false;
            },

            //获取默认收货地址和自提点
            orderConfirm: function () {

                var data = {
                    "ownerCode": this.$userInfo.OWNER_CODE,
                    "areaCode": this.$userInfo.AREA_CODE,
                    "deliveryExists":"1",
                    "shopList": this.shopList,
                    "orderSource": this.goodsData.orderSource//订单来源0购物车，1订单列表/详情，2秒杀
                }
                orderConfirmService(data).then(res => {
                    if (res.resultCode == "0") {
                        this.deliveryMode = res.data.deliveryMode;
                        this.deliveryModeDesc = res.data.deliveryModeDesc;
                        if (res.data.deliveryMode == "4") {
                            Dialog.alert({
                                message: this.deliveryModeDesc
                            }).then(() => {
                                this.goBack();
                            });
                        }
                        if (res.data.deliveryMode == "1" || res.data.deliveryMode == "2") {
                            //支持配送
                            this.recAddr = res.data.recAddrDefault;
                            if (this.recAddr) {
                                this.recName = this.recAddr.recName;
                                this.recPhone = this.recAddr.recPhone;
                            }
                            //如果返回的默认收货地址为null ，请求省市区数据
                            if (!res.data.recAddrDefault) {
                                this.queryProvince(); //获取省份
                            }
                            this.saleInfo = res.data.saleInfo;
                            this.addrInfo.canUsePointAmount = 0;
                            for (var i = 0; i < this.saleInfo.shopList.length; i++) {
                                //设置商品显示是否配送
                                this.saleInfo.shopList[i].noDistribution = false;
                                for (var j = 0; j < this.saleInfo.shopList[i].skuList.length; j++) {
                                    this.saleInfo.shopList[i].skuList[j].noDistribution = false;
                                    if (res.data.saleInfo.spuList) {
                                        if (res.data.saleInfo.spuList.length > 0) {
                                            for (var k = 0; k < res.data.saleInfo.spuList.length; k++) {
                                                if (res.data.saleInfo.spuList[k] == this.saleInfo.shopList[i].skuList[j].spuCode) {
                                                    this.saleInfo.shopList[i].skuList[j].noDistribution = true;
                                                    this.saleInfo.shopList[i].noDistribution = true;
                                                }
                                            }
                                        }
                                    }
                                }

                                //设置配送时间
                                if (this.saleInfo.shopList[i].deliveryDayList && this.saleInfo.shopList[i].deliveryDayList.length > 0) {
                                    var deliveryDayData = {
                                    };
                                    for (var k = 0; k < this.saleInfo.shopList[i].deliveryDayList.length; k++) {
                                        var deliveryDay = this.saleInfo.shopList[i].deliveryDayList[k];
                                        var deliveryTimeList = [];
                                        for (var j = 0; j < deliveryDay.timeList.length; j++) {
                                            var time = deliveryDay.timeList[j];
                                            deliveryTimeList.push(time.startTime + "-" + time.endTime);
                                        }
                                        if (deliveryTimeList.length > 0) {
                                            deliveryDayData[deliveryDay.remark] = deliveryTimeList;
                                        }
                                    }
                                    //设置默认收货时间
                                    this.saleInfo.shopList[i].deliveryDefaultString = this.saleInfo.shopList[i].deliveryDayList[0].remark + " " + this.saleInfo.shopList[i].deliveryDayList[0].timeList[0].startTime + "-" + this.saleInfo.shopList[i].deliveryDayList[0].timeList[0].endTime;
                                    this.saleInfo.shopList[i].deliveryDate = this.saleInfo.shopList[i].deliveryDayList[0].deliveryDate;
                                    this.saleInfo.shopList[i].deliveryTimeStart = this.saleInfo.shopList[i].deliveryDayList[0].timeList[0].startTime;
                                    this.saleInfo.shopList[i].deliveryTimeEnd = this.saleInfo.shopList[i].deliveryDayList[0].timeList[0].endTime;
                                    this.saleInfo.shopList[i].deliveryDayData = deliveryDayData;
                                } else {
                                    this.saleInfo.shopList[i].deliveryDefaultString = '';
                                    this.saleInfo.shopList[i].deliveryDate = '';
                                    this.saleInfo.shopList[i].deliveryTimeStart = '';
                                    this.saleInfo.shopList[i].deliveryTimeEnd = '';
                                    this.saleInfo.shopList[i].deliveryDayData = '';
                                }


                                //记录当前可用积分金额
                                if (this.saleInfo.shopList[i].shopType == "01") {
                                    //自营店铺可用积分金额
                                    this.addrInfo.canUsePointAmount += this.saleInfo.shopList[i].skuTotal;
                                }

                                //计算运费包装费
                                if (this.saleInfo.shopList[i].delivery && parseFloat(this.saleInfo.shopList[i].delivery) != 0) {
                                    this.saleInfo.shopList[i].shopDeliveryTitle = "运费" + this.saleInfo.shopList[i].delivery + "元";
                                } else {
                                    this.saleInfo.shopList[i].shopDeliveryTitle = "包邮";
                                }
                            }

                            //配送可用积分金额
                            this.canUsePointAmount = this.addrInfo.canUsePointAmount - this.saleInfo.couponPlatformTotal;
                            //设置是否允许支付标记
                            if (0 == this.saleInfo.validateResult) {
                                this.isAllowPay = false;
                            } else {
                                this.isAllowPay = true;
                            }


                        }
                        if (res.data.deliveryMode == "2" || res.data.deliveryMode == "3") {
                            //支持自提
                            this.isSelectBool = false;
                            this.pickup = res.data.recPickupDefault;
                            this.saleInfoPickup = res.data.saleInfoPickup;
                            this.pickupInfo.canUsePointAmount = 0;
                            for (var i = 0; i < this.saleInfoPickup.shopList.length; i++) {
                                //设置商品显示是否配送
                                this.saleInfoPickup.shopList[i].noDistribution = false;
                                for (var j = 0; j < this.saleInfoPickup.shopList[i].skuList.length; j++) {
                                    this.saleInfoPickup.shopList[i].skuList[j].noDistribution = false;
                                    if (res.data.saleInfoPickup.spuList) {
                                        if (res.data.saleInfoPickup.spuList.length > 0) {
                                            for (var k = 0; k < res.data.saleInfoPickup.spuList.length; k++) {
                                                if (res.data.saleInfoPickup.spuList[k] == this.saleInfoPickup.shopList[i].skuList[j].spuCode) {
                                                    this.saleInfoPickup.shopList[i].skuList[j].noDistribution = true;
                                                    this.saleInfoPickup.shopList[i].noDistribution = true;
                                                }
                                            }
                                        }
                                    }
                                }

                                if (this.saleInfoPickup.shopList[i].deliveryDayList && this.saleInfoPickup.shopList[i].deliveryDayList.length > 0) {
                                    var deliveryDayData = {

                                    };
                                    for (var k = 0; k < this.saleInfoPickup.shopList[i].deliveryDayList.length; k++) {
                                        var deliveryDay = this.saleInfoPickup.shopList[i].deliveryDayList[k];
                                        var deliveryTimeList = [];
                                        for (var j = 0; j < deliveryDay.timeList.length; j++) {
                                            var time = deliveryDay.timeList[j];
                                            deliveryTimeList.push(time.startTime + "-" + time.endTime);
                                        }
                                        if (deliveryTimeList.length > 0) {
                                            deliveryDayData[deliveryDay.remark] = deliveryTimeList;
                                        }
                                    }
                                    //设置默认收货时间
                                    this.saleInfoPickup.shopList[i].deliveryDefaultString = this.saleInfoPickup.shopList[i].deliveryDayList[0].remark + " " + this.saleInfoPickup.shopList[i].deliveryDayList[0].timeList[0].startTime + "-" + this.saleInfoPickup.shopList[i].deliveryDayList[0].timeList[0].endTime;
                                    this.saleInfoPickup.shopList[i].deliveryDate = this.saleInfoPickup.shopList[i].deliveryDayList[0].deliveryDate;
                                    this.saleInfoPickup.shopList[i].deliveryTimeStart = this.saleInfoPickup.shopList[i].deliveryDayList[0].timeList[0].startTime;
                                    this.saleInfoPickup.shopList[i].deliveryTimeEnd = this.saleInfoPickup.shopList[i].deliveryDayList[0].timeList[0].endTime;
                                    this.saleInfoPickup.shopList[i].deliveryDayData = deliveryDayData;

                                }
                                else {
                                    this.saleInfoPickup.shopList[i].deliveryDefaultString = '';
                                    this.saleInfoPickup.shopList[i].deliveryDate = '';
                                    this.saleInfoPickup.shopList[i].deliveryTimeStart = '';
                                    this.saleInfoPickup.shopList[i].deliveryTimeEnd = '';
                                    this.saleInfoPickup.shopList[i].deliveryDayData = '';
                                }
                                //记录当前可用积分金额
                                if (this.saleInfoPickup.shopList[i].shopType == "01") {
                                    //自营店铺可用积分金额
                                    this.pickupInfo.canUsePointAmount += this.saleInfoPickup.shopList[i].skuTotal;
                                }
                                if (this.saleInfoPickup.shopList[i].delivery && parseFloat(this.saleInfoPickup.shopList[i].delivery) != 0) {
                                    this.saleInfoPickup.shopList[i].shopDeliveryTitle = "运费" + this.saleInfoPickup.shopList[i].delivery + "元";
                                } else {
                                    this.saleInfoPickup.shopList[i].shopDeliveryTitle = "包邮";
                                }


                            }
                            this.showInfo = this.saleInfoPickup;

                            //自提可用积分金额
                            this.canUsePointAmount = this.pickupInfo.canUsePointAmount - this.saleInfoPickup.couponPlatformTotal;


                            //设置是否允许支付标记
                            if (0 == this.saleInfoPickup.pickupResult) {
                                this.isAllowPayPickup = false;
                            } else {
                                this.isAllowPayPickup = true;
                            }
                        }
                        else {
                            //仅支持配送情况下
                            this.isSelectBool = true;
                            this.showInfo = this.saleInfo;
                            //自提可用积分金额
                            this.canUsePointAmount = this.addrInfo.canUsePointAmount;

                        }
                        this.$forceUpdate();
                        //获取可用积分
                        this.getUseOrderLimitFn();
                    } else {
                        Dialog.alert({
                            message: res.msg
                        }).then(() => {
                            this.goBack();
                        });
                    }
                })
                    .catch(error => {
                        Dialog.alert({
                            message: '网络异常，请稍后重试！'
                        }).then(() => {
                            this.goBack();
                        });
                    })
            },
            // 获取省份
            queryProvince: function () {
                var data = {
                    areaCode: this.$userInfo.AREA_CODE
                }
                queryProvinceService(data).then(res => {

                    if (res.resultCode == 0) {
                        this.selectData = res.data;
                        console.log(this.selectData)
                        this.selectDataIndex = 0; // 省标志
                        this.selectIndex = -1;
                    }
                    console.log(res)
                }).catch(error => {
                })
            },
            // 获取市
            getCityList: function (name) {
                var data = {
                    provinceName: name,
                    areaCode: this.$userInfo.AREA_CODE
                }

                getCityListService(data).then(res => {

                    if (res.resultCode == 0) {
                        this.selectData = res.data;
                        this.selectDataIndex = 1; // 市标志
                        this.selectIndex = -1;
                    }
                    console.log(res)
                }).catch(error => {
                })
            },
            // 获取区
            queryDistrict: function (name) {
                var data = {
                    cityName: name,
                    areaCode: this.$userInfo.AREA_CODE
                }

                queryDistrictService(data).then(res => {

                    if (res.resultCode == 0) {
                        this.selectData = res.data;
                        this.selectDataIndex = 2; // 区标志
                        this.selectIndex = -1;
                    }
                    console.log(res)
                }).catch(error => {
                })
            },
            // 获取小区
            getAreaList: function (name) {
                var data = {
                    districtName: name,
                    areaCode: this.$userInfo.AREA_CODE
                }

                getAreaListService(data).then(res => {

                    if (res.resultCode == 0) {
                        this.selectData = res.data;
                        this.selectDataIndex = 3; // 小区标志
                        this.selectIndex = -1;
                    }
                    console.log(res)
                }).catch(error => {
                })
            },

            //选择的数据
            selectCity: function (index) {
                this.selectIndex = index;
                //选择的城市
                if (this.selectDataIndex == 0) {
                    this.regionData.recProvince = this.selectData[index].provinceName;
                    this.regionData.recCity = ""
                    this.regionData.recDistrict = ""
                    this.regionData.recAreaName = ""
                    this.getCityList(this.selectData[index].provinceName);
                    this.$refs.adressSelectContent.scrollTop = 0;
                } else if (this.selectDataIndex == 1) {
                    this.regionData.recCity = this.selectData[index].cityName;
                    this.regionData.recDistrict = ""
                    this.regionData.recAreaName = ""
                    this.queryDistrict(this.selectData[index].cityName);
                    this.$refs.adressSelectContent.scrollTop = 0;
                } else if (this.selectDataIndex == 2) {
                    this.regionData.recDistrict = this.selectData[index].districName;
                    this.regionData.recAreaName = "";
                    if (this.isVirtualArea) {
                        //如果是虚拟小区
                        this.hideSelectBox();
                        return;
                    }
                    this.getAreaList(this.selectData[index].districName);
                    this.$refs.adressSelectContent.scrollTop = 0;
                } else if (this.selectDataIndex == 3) {
                    this.regionData.recAreaName = this.selectData[index].areaName;
                    this.regionData.recAreaCode = this.selectData[index].areaCode;
                    this.defaultRegionData.recAreaName = this.selectData[index].areaName;
                    this.defaultRegionData.recAreaCode = this.selectData[index].areaCode;
                    this.$refs.adressSelectContent.scrollTop = 0;
                    this.changeRecAddr();
                    this.hideSelectBox();
                }
            },
            //校验收货地址 新
            changeRecAddr: function () {
                var data = {}

                if (this.recAddr) {
                    data = {
                        "recAreaCode": this.recAddr.recAreaCode,
                        "saleInfo": this.saleInfo,
                        "deliveryExists": "1",
                        "ownerCode": this.$userInfo.OWNER_CODE,
                        "orderSource": this.goodsData.orderSource//订单来源0购物车，1订单列表/详情，2秒杀
                    }
                } else {
                    data = {
                        "recAreaCode": this.defaultRegionData.recAreaCode,
                        "saleInfo": this.saleInfo,
                        "deliveryExists": "1",
                        "ownerCode": this.$userInfo.OWNER_CODE,
                        "orderSource": this.goodsData.orderSource//订单来源0购物车，1订单列表/详情，2秒杀
                    }
                }
                changeRecAddrService(data).then(res => {
                    //处理不在配送范围内显示shopItem.noDistribution
                    if ("0" == res.resultCode) {
                        this.saleInfo = res.data.saleInfo;
                        this.addrInfo.canUsePointAmount = 0;
                        for (var i = 0; i < this.saleInfo.shopList.length; i++) {
                            //设置商品显示是否配送
                            this.saleInfo.shopList[i].noDistribution = false;
                            for (var j = 0; j < this.saleInfo.shopList[i].skuList.length; j++) {
                                this.saleInfo.shopList[i].skuList[j].noDistribution = false;
                                if (this.saleInfo.spuList && this.saleInfo.spuList.length > 0) {
                                    for (var k = 0; k < this.saleInfo.spuList.length; k++) {
                                        if (this.saleInfo.spuList[k] == this.saleInfo.shopList[i].skuList[j].spuCode) {
                                            this.saleInfo.shopList[i].skuList[j].noDistribution = true;
                                            this.saleInfo.shopList[i].noDistribution = true;
                                        }
                                    }
                                }

                            }

                            //设置配送时间
                            if (this.saleInfo.shopList[i].deliveryDayList && this.saleInfo.shopList[i].deliveryDayList.length > 0) {
                                var deliveryDayData = {
                                };
                                for (var k = 0; k < this.saleInfo.shopList[i].deliveryDayList.length; k++) {
                                    var deliveryDay = this.saleInfo.shopList[i].deliveryDayList[k];
                                    var deliveryTimeList = [];
                                    for (var j = 0; j < deliveryDay.timeList.length; j++) {
                                        var time = deliveryDay.timeList[j];
                                        deliveryTimeList.push(time.startTime + "-" + time.endTime);
                                    }
                                    if (deliveryTimeList.length > 0) {
                                        deliveryDayData[deliveryDay.remark] = deliveryTimeList;
                                    }
                                }
                                //设置默认收货时间
                                this.saleInfo.shopList[i].deliveryDefaultString = this.saleInfo.shopList[i].deliveryDayList[0].remark + " " + this.saleInfo.shopList[i].deliveryDayList[0].timeList[0].startTime + "-" + this.saleInfo.shopList[i].deliveryDayList[0].timeList[0].endTime;
                                this.saleInfo.shopList[i].deliveryDate = this.saleInfo.shopList[i].deliveryDayList[0].deliveryDate;
                                this.saleInfo.shopList[i].deliveryTimeStart = this.saleInfo.shopList[i].deliveryDayList[0].timeList[0].startTime;
                                this.saleInfo.shopList[i].deliveryTimeEnd = this.saleInfo.shopList[i].deliveryDayList[0].timeList[0].endTime;
                                this.saleInfo.shopList[i].deliveryDayData = deliveryDayData;
                            } else {
                                this.saleInfo.shopList[i].deliveryDefaultString = '';
                                this.saleInfo.shopList[i].deliveryDate = '';
                                this.saleInfo.shopList[i].deliveryTimeStart = '';
                                this.saleInfo.shopList[i].deliveryTimeEnd = '';
                                this.saleInfo.shopList[i].deliveryDayData = '';
                            }

                            //记录当前可用积分金额
                            if (this.saleInfo.shopList[i].shopType == "01") {
                                //自营店铺可用积分金额
                                this.addrInfo.canUsePointAmount += this.saleInfo.shopList[i].skuTotal;
                            }

                            //运费
                            if (this.saleInfo.shopList[i].delivery && parseFloat(this.saleInfo.shopList[i].delivery) != 0) {
                                this.saleInfo.shopList[i].shopDeliveryTitle = "运费" + this.saleInfo.shopList[i].delivery + "元";
                            } else {
                                this.saleInfo.shopList[i].shopDeliveryTitle = "包邮";
                            }


                        }

                        //计算订单金额
                        this.canUsePointAmount = this.addrInfo.canUsePointAmount - this.saleInfo.couponPlatformTotal;
                        //设置是否允许支付标记
                        if (0 == res.data.validateResult) {
                            this.isAllowPay = false;
                        } else {
                            this.isAllowPay = true;
                        }
                        this.showInfo = this.saleInfo;
                        this.$forceUpdate();
                        //获取可用积分
                        this.getUseOrderLimitFn();


                    } else {
                        Dialog.alert({
                            message: res.msg
                        }).then(() => {
                            this.goBack();
                        });
                    }
                })
                    .catch(error => {
                        Dialog.alert({
                            message: '网络异常，请稍后重试！'
                        }).then(() => {
                            this.goBack();
                        });
                    })
            },

            // 获取用户可用积分
            getUseOrderLimitFn: function () {

                //重置抵扣积分和抵扣钱数
                this.pointAviablePersent = "0.00";
                this.integralDeduction = 0;
                this.integralAll = false;
                var data = {
                    ownerCode: this.$userInfo.OWNER_CODE,
                    money: this.canUsePointAmount
                }
                console.log(data);
                getUseOrderLimitService(data).then(res => {
                    console.log(res);
                    if (res.code == 200) {//true表示返回成功
                        this.pointAviable = Math.floor(res.data / 100) * 100;
                    } else {
                        this.pointAviable = 0;
                    }
                }).catch(error => {
                })
            },

            //切换优惠券
            setBtnItemIndex: function (index) {
                this.btnItemIndex = index;
                this.$forceUpdate();
            },
            //顶部切换选择按钮
            switchBtn: function (isBool) {
                if (isBool == this.isSelectBool ) {
                    return;
                }

                if (this.deliveryMode == 1 || this.deliveryMode == 3) {
                    Toast(this.deliveryModeDesc);
                    return;
                }
                if (isBool == true) {
                    this.canUsePointAmount = this.addrInfo.canUsePointAmount;
                    this.showInfo = this.saleInfo;

                    this.changeRecAddr();
                } else {
                    this.canUsePointAmount = this.pickupInfo.canUsePointAmount;
                    this.showInfo = this.saleInfoPickup;
                }
                this.$forceUpdate();
                //获取可用积分
                this.getUseOrderLimitFn();

                this.isSelectBool = isBool;
            },
