            shopCouponMore: function (shopItem, shopIndex) {
                this.couponUsefulList = [];
                this.couponUselessCount = 0;
                this.couponUsefulCount = 0;
                
                if (shopItem.couponUsefulList && shopItem.couponUsefulList.length > 0) {
                    shopItem.couponUsefulList.isSelect = false;
                    for (var i = 0; i < shopItem.couponUsefulList.length; i++) {
                        shopItem.couponUsefulList[i].isSelect = false;
                        if (shopItem.couponUsefulList[i].couponCode == shopItem.couponConfirm.couponCode) {
                            shopItem.couponUsefulList[i].isSelect = true;
                            shopItem.couponUsefulList.isSelect = true;
                        }
                    }
                    this.couponUsefulCount = shopItem.couponUsefulList.length;
                    this.couponUsefulList.push(shopItem.couponUsefulList);
                }
                this.couponUselessList = [];
                if (shopItem.couponUselessList && shopItem.couponUselessList.length > 0) {
                    this.couponUselessCount = shopItem.couponUselessList.length;
                    this.couponUselessList.push(shopItem.couponUselessList);

                }
                this.couponListType = shopIndex;
                this.btnItemIndex = 0;
                this.isShowCoverUp = true;
                console.log(this.couponUselessList);
                console.log(this.couponUsefulList);
            },

