                if (this.showInfo.platShopUselessList && this.showInfo.platShopUselessList.length > 0) {
                    for (var i = 0; i < this.showInfo.platShopUselessList.length; i++) {
                        //店铺列表couponList   this.showInfo.platShopUsefulList[i].couponList

                        this.couponUselessCount += this.showInfo.platShopUselessList[i].couponList.length;
