            selectedCoupon: function (couponUsefulListItem, couponIndex, couponItem) {
                
                if (couponItem.isSelect) {
                    couponItem.isSelect = false;
                    couponUsefulListItem.isSelect = false;
                } else {
                    couponUsefulListItem.isSelect = true;
                    for (var i = 0; i < couponUsefulListItem.length; i++) {
                        couponUsefulListItem[i].isSelect = false;
                        if (i == couponIndex) {
                            couponUsefulListItem[i].isSelect = true;
                        }
                    }
                }
                this.$forceUpdate();

            },
            //跳转优惠券对应店铺
            goToShopPage(couponItem) {
                if (couponItem.type == '01') {
                    let shopCodeList = [];
                    this.$router.push({
                        name: "goodsCollection",
                        params: {
                            couponName: couponItem.name,
                            shopCodeList: shopCodeList,
                            isOrderPayNew: true,
                        }
                    });
                } else if (couponItem.type == '02') {
                    this.$router.push({ name: "shopPage", params: { shopCode: couponItem.shopCode, flag: 0 } });
                } else {
                    let shopCodeList = "";
                    couponItem.couponShopList.map((shopItem, shopIndex) => {
                        if (shopIndex == 0) {
                            shopCodeList = shopItem.shopCode;
                        } else {
                            shopCodeList = shopCodeList + ',' + shopItem.shopCode;
                        }
                    })
                    this.$router.push({
                        name: "goodsCollection",
                        params: {
                            couponName: couponItem.name,
                            shopCodeList: shopCodeList,
                        }
                    });
                }
            },
            //查看适应范围
            showAndHideScope(couponItem) {
                console.log(couponItem)
                couponItem.isShowScope = !couponItem.isShowScope;
                this.$forceUpdate();
            },

            //选中优惠券后点击完成按钮
            completeBtn: function () {
                if (this.couponListType == -1) {
                    //平台券
                    var selectCouponUsefulList = [];
                    for (var i = 0; i < this.couponUsefulList.length; i++) {
                        for (var j = 0; j < this.couponUsefulList[i].length; j++) {
                            if (this.couponUsefulList[i][j].isSelect) {
                                selectCouponUsefulList.push(this.couponUsefulList[i][j]);
                            }
                        }
                    }
                    this.showInfo.couponPlatformList = selectCouponUsefulList;



                } else {
                    //店铺券
                    this.showInfo.shopList[this.couponListType].couponConfirm = { 'couponTotal': 0 }
                    console.log("console.log(this.couponUsefulList);");
                    console.log(this.couponUsefulList);
                    if(this.couponUsefulList.length>0){
                        for (var j = 0; j < this.couponUsefulList[0].length; j++) {
                            if (this.couponUsefulList[0][j].isSelect) {
                                this.showInfo.shopList[this.couponListType].couponConfirm = this.couponUsefulList[0][j];
                            }
                        }
                    }


                }

                console.log(this.showInfo);
                //获取可用积分
                this.refreshSaleInfoFn();

