            refreshSaleInfoFn: function () {
                refreshSaleInfoService(this.showInfo).then(res => {
                    this.isShowCoverUp = false;
                    if (res.resultCode == "0") {
                        this.showInfo = res.data;

                        this.canUsePointAmount = 0;
                        for (var i = 0; i < this.showInfo.shopList.length; i++) {
                            //设置商品显示是否配送
                            this.showInfo.shopList[i].noDistribution = false;
                            for (var j = 0; j < this.showInfo.shopList[i].skuList.length; j++) {
                                this.showInfo.shopList[i].skuList[j].noDistribution = false;
                                if (this.showInfo.spuList) {
                                    if (res.data.spuList.length > 0) {
                                        for (var k = 0; k < res.data.spuList.length; k++) {
                                            if (res.data.spuList[k] == this.showInfo.shopList[i].skuList[j].spuCode) {
                                                this.showInfo.shopList[i].skuList[j].noDistribution = true;
                                                this.showInfo.shopList[i].noDistribution = true;
                                            }
                                        }
                                    }
                                }
                            }

                            //设置配送时间
                            if (this.showInfo.shopList[i].deliveryDayList && this.showInfo.shopList[i].deliveryDayList.length > 0) {
                                var deliveryDayData = {
                                };
                                for (var k = 0; k < this.showInfo.shopList[i].deliveryDayList.length; k++) {
                                    var deliveryDay = this.showInfo.shopList[i].deliveryDayList[k];
                                    var deliveryTimeList = [];
                                    for (var j = 0; j < deliveryDay.timeList.length; j++) {
                                        var time = deliveryDay.timeList[j];
                                        deliveryTimeList.push(time.startTime + "-" + time.endTime);
                                    }
                                    if (deliveryTimeList.length > 0) {
                                        deliveryDayData[deliveryDay.remark] = deliveryTimeList;
                                    }
                                }
                                //设置默认收货时间
                                this.showInfo.shopList[i].deliveryDefaultString = this.showInfo.shopList[i].deliveryDayList[0].remark + " " + this.showInfo.shopList[i].deliveryDayList[0].timeList[0].startTime + "-" + this.showInfo.shopList[i].deliveryDayList[0].timeList[0].endTime;
                                this.showInfo.shopList[i].deliveryDate = this.showInfo.shopList[i].deliveryDayList[0].deliveryDate;
                                this.showInfo.shopList[i].deliveryTimeStart = this.showInfo.shopList[i].deliveryDayList[0].timeList[0].startTime;
                                this.showInfo.shopList[i].deliveryTimeEnd = this.showInfo.shopList[i].deliveryDayList[0].timeList[0].endTime;
                                this.showInfo.shopList[i].deliveryDayData = deliveryDayData;
                            } else {
                                this.showInfo.shopList[i].deliveryDefaultString = '';
                                this.showInfo.shopList[i].deliveryDate = '';
                                this.showInfo.shopList[i].deliveryTimeStart = '';
                                this.showInfo.shopList[i].deliveryTimeEnd = '';
                                this.showInfo.shopList[i].deliveryDayData = '';
                            }


                            //记录当前可用积分金额
                            if (this.showInfo.shopList[i].shopType == "01") {
                                //自营店铺可用积分金额
                                this.canUsePointAmount += this.showInfo.shopList[i].skuTotal;
                            }

                            //计算运费包装费
                            if (this.showInfo.shopList[i].delivery && parseFloat(this.showInfo.shopList[i].delivery) != 0) {
                                this.showInfo.shopList[i].shopDeliveryTitle = "运费" + this.showInfo.shopList[i].delivery + "元";
                            } else {
                                this.showInfo.shopList[i].shopDeliveryTitle = "包邮";
                            }
                        }

                        //配送可用积分金额
                        this.canUsePointAmount = this.canUsePointAmount - this.showInfo.couponPlatformTotal;



                        this.getUseOrderLimitFn();
                    } else {
                        Dialog.alert({
                            message: res.msg
                        }).then(() => {
                            this.goBack();
                        });
                    }

                })
                    .catch(error => {
                        this.isShowCoverUp = false;
                        Dialog.alert({
                            message: '网络异常，请稍后重试！'
                        }).then(() => {
                            this.goBack();
                        });
                    })
            },
            //下单前校验收货地址

            checkAddresBeforecreateOrder: function () {


                if ((this.showInfo.realPayTotal - parseFloat(this.pointAviablePersent)) == 0) {
                    this.$toast("实付金额不能为0");
                    return;
                }
                if ((this.showInfo.realPayTotal - parseFloat(this.pointAviablePersent)) < 0) {
                    this.$toast("实付金额不能小于0");
                    return;
                }

                if (this.isSelectBool == true) { //送货上门
                    if (this.recName == "") {
                        this.$toast("请输入收货人姓名");
                        return;
                    }

                    var str = /^1[0-9]{10}$/;
                    if (this.recPhone == "") {
                        this.$toast("请输入收货人手机号码");
                        return;
                    } else if (!str.test(this.recPhone)) {
                        this.$toast("请输入正确的手机号码");
                        return;
                    }


                    var addressData = {};



                    if (!this.recAddr) {
                        //如果是没有默认收货地址，调用保存收货地址接口
                        if ((this.regionData.recAreaName == '' || this.regionData.recAreaName == undefined || this.regionData.recAreaName == null) && this.isVirtualArea) {
                            this.$toast("请选择所在地区");
                            return;
                        }
                        // if ((this.areaLongitude ==null || this.areaLongitude == "" || this.areaLongitude == undefined) && (this.isVirtualArea)) {
                        //     Dialog.confirm({
                        //         message: '请重新选择收货地址，方便给您配送'
                        //     }).then(() => {
                        //         // 确定
                        //     }).catch(() => {
                        //         // on cancel
                        //     });
                        //     return;
                        // }

                        if (this.recAddressDetail == "") {
                            this.$toast("请输入门牌号");
                            return;
                        }



                        addressData = {
                            "ownerCode": this.$userInfo.OWNER_CODE,
                            "isDefault": 1,
                            "recName": this.recName,
                            "recPhone": this.recPhone,
                            "recAreaName": this.regionData.recAreaName,
                            "recAreaCode": this.regionData.recAreaCode,
                            "areaLongitude": this.regionData.areaLongitude,
                            "areaLatitude": this.regionData.areaLatitude,
                            "recAddressDetail": this.recAddressDetail,
                            "recProvince": this.regionData.recProvince,
                            "recCity": this.regionData.recCity,
                            "recDistrict": this.regionData.recDistrict,
                            "areaCode": this.$userInfo.AREA_CODE,
                            "areaName": this.$userInfo.AREA_NAME
                        }
                        this.saveAddress(addressData);

                    } else {
                        if (this.recName != this.recAddr.recName || this.recPhone != this.recAddr.recPhone) {
                            //修改过收货人和收货电话
                            addressData = {
                                "ownerCode": this.$userInfo.OWNER_CODE,
                                "recName": this.recName,
                                "recPhone": this.recPhone,
                                "recAreaName": this.recAddr.recAreaName,
                                "recAreaCode": this.recAddr.recAreaCode,
                                "recAddressDetail": this.recAddr.recAddressDetail,
                                "recCode": this.recAddr.recCode,
                                "recProvince": this.regionData.recProvince,
                                "recCity": this.regionData.recCity,
                                "recDistrict": this.regionData.recDistrict,
                                "isDefault": this.recAddr.isDefault,
                                "areaCode": this.$userInfo.AREA_CODE,
                                "areaName": this.$userInfo.AREA_NAME
                            }
                            this.saveAddress(addressData);
                        } else {

                            var data = {
                                "ownerCode": this.$userInfo.OWNER_CODE,
                                "ownerName": this.$userInfo.OWNER_NAME,
                                "pointCost": this.integralDeduction,
                                "pointTotal": this.pointAviablePersent,
                                "realTotal": this.showInfo.realPayTotal - parseFloat(this.pointAviablePersent),
                                "billTotal": this.showInfo.shouldPayTotal,
                                "couponPlatformList": this.showInfo.couponPlatformList,
                                "couponPlatformTotal": this.showInfo.couponPlatformTotal,
                                "recName": this.recAddr.recName,
                                "recAreaCode": this.recAddr.recAreaCode,
                                "recPhone": this.recAddr.recPhone,
                                "recAddr": this.recAddr.recAddr,
                                "recCode": this.recAddr.recCode,
                                "orderSource": this.goodsData.orderSource,//订单来源0购物车，1订单列表/详情，2秒杀
                                "shopList": this.showInfo.shopList,
                                "deliveryType": '1',
                                "areaCode": this.$userInfo.AREA_CODE,
                                "roomCode": this.$userInfo.ROOM_CODE,
                                "roomName": this.$userInfo.ROOM_NAME,
                                "nostoreString": this.nostoreString,
                                "nostoreChoice": this.nostoreChoice,
                                "actExists": 1,//标识新app
                                "channelId":storage.getStorage("channelId", false).message !="数据不存在" ? storage.getStorage("channelId", false):null,
				                "version":Config.version
                            }
                            if (this.isSaveOrder) {
                                return;
                            }
                            this.isSaveOrder = true;
                            this.createOrder(data);

                        }

                    }
                }
                else {  //自提
                    if (this.ownerName == "") {
                        this.$toast("请输入自提人姓名");
                        return;
                    }

                    let str = /^1[0-9]{10}$/;
                    if (this.ownerPhone == "") {
                        this.$toast("请输入自提人手机号码");
                        return;
                    } else if (!str.test(this.ownerPhone)) {
                        this.$toast("请输入正确的手机号码");
                        return;
                    }
                    var pickupData =
                        {
                            pickupName: this.ownerName,
                            pickupPhone: this.ownerPhone,
                        }
                    storage.setStorage("pickupData", pickupData, 999);
                    var data = {
                        "ownerCode": this.$userInfo.OWNER_CODE,
                        "ownerName": this.$userInfo.OWNER_NAME,
                        "pointCost": this.integralDeduction,
                        "pointTotal": this.pointAviablePersent,
                        "realTotal": this.showInfo.realPayTotal - parseFloat(this.pointAviablePersent),
                        "billTotal": this.showInfo.shouldPayTotal,
                        "couponPlatformList": this.showInfo.couponPlatformList,
                        "couponPlatformTotal": this.showInfo.couponPlatformTotal,
                        "pickupName": this.pickup.pickupName,
                        "pickupPhone": this.pickup.pickupPhone,
                        "recAddr": this.pickup.pickupAddr,
                        "recCode": this.pickup.id,
                        "orderSource": this.goodsData.orderSource,//订单来源0购物车，1订单列表/详情，2秒杀
                        "shopList": this.showInfo.shopList,
                        "deliveryType": '2',
                        "recName": this.ownerName,
                        "recAreaCode": this.$userInfo.AREA_CODE,
                        "recPhone": this.ownerPhone,
                        "areaCode": this.$userInfo.AREA_CODE,
                        "roomCode": this.$userInfo.ROOM_CODE,
                        "roomName": this.$userInfo.ROOM_NAME,
                        "nostoreString": this.nostoreString,
                        "nostoreChoice": this.nostoreChoice,
                        "actExists": 1,//标识新app
                        "channelId":storage.getStorage("channelId", false) !=undefined ? storage.getStorage("channelId", false):null,
                    	"version":Config.version
		    }
                    if (this.isSaveOrder) {
                        return;
                    }
                    this.isSaveOrder = true;
                    this.createOrder(data);

                }

            },


            saveAddress: function (recAdressData) {
                if (this.isSaveOrder) {
                    return;
                }
                this.isSaveOrder = true;
                quickAddRecAddrService(recAdressData).then(res => {
                    this.isSaveOrder = false;
                    if (res.resultCode == 0) {
                        var data = {
                            "ownerCode": this.$userInfo.OWNER_CODE,
                            "ownerName": this.$userInfo.OWNER_NAME,
                            "pointCost": this.integralDeduction,
                            "pointTotal": this.pointAviablePersent,
                            "realTotal": this.showInfo.realPayTotal - parseFloat(this.pointAviablePersent),
                            "billTotal": this.showInfo.shouldPayTotal,
                            "couponPlatformList": this.showInfo.couponPlatformList,
                            "couponPlatformTotal": this.showInfo.couponPlatformTotal,
                            "recName": res.data.recAddr.recName,
                            "recAreaCode": res.data.recAddr.recAreaCode,
                            "recPhone": res.data.recAddr.recPhone,
                            "recAddr": res.data.recAddr.recAddr,
                            "recCode": res.data.recAddr.recCode,
                            "orderSource": this.goodsData.orderSource,//订单来源0购物车，1订单列表/详情，2秒杀
                            "shopList": this.showInfo.shopList,
                            "deliveryType": '1',
                            "areaCode": this.$userInfo.AREA_CODE,
                            "roomCode": this.$userInfo.ROOM_CODE,
                            "roomName": this.$userInfo.ROOM_NAME,
                            "nostoreString": this.nostoreString,
                            "nostoreChoice": this.nostoreChoice,
                            "actExists": 1,//标识新app,
                            "channelId":storage.getStorage("channelId", false) !=undefined ? storage.getStorage("channelId", false):null,
                            "version":Config.version
                        }
                        this.createOrder(data);

                    } else {
                        Toast(res.msg);
                    }
                    console.log(res)
                }).catch(error => {
                    this.isSaveOrder = false;
                })

            },

            // 提交用户积分 保存订单
            createOrder: function (orderData) {
                createOrderService(orderData).then(res => {
                    this.isSaveOrder = false;
                    if ("0" == res.resultCode) {//true表示返回成功
                        this.payParam = res.data.unionPayParam;
                        this.payParam.billNo = res.data.billNo;
                        this.$userInfo.PRODUCT_BILL_CODE = res.data.billNo;//记录最后支付订单号
                        console.log("订单创建成功" + this.$userInfo.PRODUCT_BILL_CODE);

                        //生成订单后移除localstorage内的收货地址
                        let recItem = storage.getStorage("recItem");
                        if (!recItem.$s_code_$exp) {
                            storage.deleteStorage("recItem");
                        }
                        this.payWindowShow = true;
                        this.isAlipay = true;
                        this.whichPay(this.$userInfo.PRODUCT_BILL_CODE, this.billCodePoint, this.payParam);
                    }
                    else if ("20102" == res.resultCode) {
                        //如果code为20102时，需要重新选择收货时间
                        for (var i = 0; i < res.data.shopList.length; i++) {

                            if (this.isSelectBool) {
                                //配送
                                for (var m = 0; m < this.saleInfo.shopList.length; m++) {
                                    if (res.data.shopList[i].shopCode == this.saleInfo.shopList[m].shopCode) {
