            //输入积分数值
            changePointNum: function () {
                this.integralDeduction = this.integralDeduction.replace(/[^\d]/g, '')
                if (this.integralDeduction == "") {
                    this.integralDeduction = 0;
                    // return;
                }

                this.integralDeduction = Number(this.integralDeduction);
                if (this.integralDeduction > this.pointAviable) {
                    this.integralDeduction = Math.floor(this.pointAviable / 100) * 100;
                    this.msg = '本单最高抵扣' + this.pointAviable + '积分';
                    this.$toast(this.msg);
                } else if (this.integralDeduction < 0) {
                    this.integralDeduction = 0;
                    this.msg = '抵扣积分不能为负数';
                    this.$toast(this.msg);
                } else {
                    if (this.integralDeduction % 100 != 0) {
                        this.msg = '抵扣积分为整百';
                        this.integralDeduction = Math.floor(this.integralDeduction / 100) * 100;
                        this.$toast(this.msg);
                    }
                }
                console.log(this.integralDeduction)
                this.pointAviablePersent = (this.integralDeduction / 100).toFixed(2);
            },

            //按钮加
            numberAdd: function (intergral) {
                if (!this.$userInfo.ROOM_CODE) {
                    Toast("积分不可用");
                    return;
                }
                if (intergral == 0) {
                    Toast("暂无可用积分");
                    return;
                }

                if (this.integralDeduction < intergral) {
                    if (this.integralDeduction + 100 <= intergral) {
                        this.integralDeduction = this.integralDeduction + 100;
                    }
                    else {
                        this.$toast('本次可用最高积分为' + this.integralDeduction);
                    }
                } else {

                    this.integralDeduction = intergral;
                    this.$toast('本次可用最高积分为' + this.integralDeduction);
                }
                this.pointAviablePersent = (this.integralDeduction / 100).toFixed(2);//最多可扣积分
            },

            //按钮减
            numberReduce: function () {
                if (!this.$userInfo.ROOM_CODE) {
                    return;
                }

                if (this.integralDeduction == 0) {
                    this.$toast("已经是最低了，不能在减少了哦！");
                    this.integralDeduction = 0;
                } else {
                    if (this.integralDeduction - 100 < 0) {
                        this.integralDeduction = 0;
                    } else {
                        this.integralDeduction = this.integralDeduction - 100;
                    }
                }
                this.pointAviablePersent = (this.integralDeduction / 100).toFixed(2);//最多可扣积分
            },

            //是否全部积分抵扣
            integralAllFn: function (intergrals) {
                if (!this.$userInfo.ROOM_CODE) {
                    this.integralAll = false;
                    Toast("积分不可用");
                    return;
                }
                console.log(intergrals / 100);
                if (intergrals == 0) {
                    Toast("暂无可用积分");
                    this.integralAll = false;
                    return;
                }


                console.log(this.integralAll);
                if (this.integralAll) {
                    this.integralDeduction = Math.floor(intergrals / 100) * 100;
                } else {
                    this.integralDeduction = 0;
                }
                this.pointAviablePersent = (this.integralDeduction / 100).toFixed(2);//最多可扣积分
            },

